@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Montserrat:ital,wght@0,100;0,200;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");

.box {
  @apply bg-blue text-white dark:bg-white dark:text-black rounded-md capitalize;
}

.button_generic {
  @apply rounded-full border-blue border-2 cursor-pointer sm:text-[18px] px-4 flex items-center justify-center gap-2 py-[10px];
}

.light {
  background: radial-gradient(
    50% 50% at 50% 50%,
    rgba(199, 62, 29, 0.23) 0%,
    rgba(199, 62, 29, 0) 100%
  );
  height: 200%;
  top: -50%;
  left: 50%;
  width: 70%;
  transform: translateX(-50%);
  z-index: -999;
}
.MenuItem {
  @apply p-2 text-black dark:text-[#e8e6e3] hover:bg-blue hover:text-white dark:hover:bg-white w-fit cursor-pointer dark:hover:text-black rounded-md capitalize;
}
.xl-title {
  @apply font-[800] text-4xl sm:text-[64px] text-darkGray font-inter sm:leading-[70px];
}

section > div {
  @apply max-w-6xl mx-auto p-4;
}

#home {
  @apply h-screen flex flex-col gap-10 justify-center items-center min-h-[500px] max-h-[800px];
}
/******************************/

html.has-scroll-smooth {
  overflow: hidden;
}

html.has-scroll-dragging {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.has-scroll-smooth body {
  overflow: hidden;
}

.has-scroll-smooth [data-scroll-container] {
  min-height: 100vh;
}

[data-scroll-direction="horizontal"] [data-scroll-container] {
  height: 100vh;
  display: inline-block;
  white-space: nowrap;
}

[data-scroll-direction="horizontal"] [data-scroll-section] {
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
  height: 100%;
}

.c-scrollbar {
  position: absolute;
  right: 0;
  top: 0;
  width: 11px;
  height: 100%;
  transform-origin: center right;
  transition: transform 0.3s, opacity 0.3s;
  opacity: 0;
}

.c-scrollbar:hover {
  transform: scaleX(1.45);
}

.c-scrollbar:hover,
.has-scroll-scrolling .c-scrollbar,
.has-scroll-dragging .c-scrollbar {
  opacity: 1;
}

[data-scroll-direction="horizontal"] .c-scrollbar {
  width: 100%;
  height: 10px;
  top: auto;
  bottom: 0;
  transform: scaleY(1);
}

[data-scroll-direction="horizontal"] .c-scrollbar:hover {
  transform: scaleY(1.3);
}

.c-scrollbar_thumb {
  position: absolute;
  top: 0;
  right: 0;
  background-color: black;
  opacity: 0.5;
  width: 7px;
  border-radius: 10px;
  margin: 2px;
  cursor: -webkit-grab;
  cursor: grab;
}
.has-scroll-dragging .c-scrollbar_thumb {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
[data-scroll-direction="horizontal"] .c-scrollbar_thumb {
  right: auto;
  bottom: 0;
}
